import React from "react";
//dependencias
import * as palabraClaveServer from "../../services/palabraClave";

//components
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import { makeStyles } from "@material-ui/core/styles";
import "../../styles/cloud.css";


const useStyles = makeStyles((theme) => ({
  
  containerModal:{
    border: "2px solid #ccc",
    borderRadius: 50,
    boxShadow: "4px 4px 4px 0px #5a28e550",
  },
  containerForm: {
    height:"3rem",
    background: "#BFF",
    border: "2px solid #ccc",
    borderRadius: 50,
    boxShadow: "4px 4px 4px 0px #5a28e550",
  },

  "@media (max-width: 720px)": {
    root: {
      display: "flex",
    },
  },
}));
const ModalPalabraClave = ({ handleClose, show, palabraclave_id,currentItem, setFormData, newData, ...props}) => {

  const handleInputChange = (e) => {
    let target = e.target;
    let name = target.name;
    setFormData({...newData, [name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const formData = new FormData();

    try {
      if (!palabraclave_id) {
        formData.append("palabraClave", newData.palabraClave);
        await palabraClaveServer.RegisterPalabraClave(formData);
      } else {
        formData.append("palabraClave", newData.palabraClave);
        await palabraClaveServer.updatePalabraClave(palabraclave_id, formData);        
      }
      const res = await palabraClaveServer.ListPalabrasClaves();
      const data = await res;
      props.setPalabrasClaves(data);
    } catch (error) {
      console.log(error);      
    }
  }
  
  const classes = useStyles();
  return (
    <Modal 
      show={show} 
      onHide={handleClose}
      aria-labelledby="example-custom-modal-styling-title"
      contentClassName = {classes.cloud}
      centered
    >
      <Modal.Header closeButton >
        <Modal.Title id="tituloModal" className="text-dark mx-2 mt-2" ><h4>{palabraclave_id? 'Editar Registro' : 'Registrar'} Palabra clave</h4></Modal.Title>

      </Modal.Header>
      <Modal.Body id="bodyModal" >
        <Form onSubmit={handleSubmit}  >
          <Form.Group className={"mb-2 "} controlId="exampleForm.ControlInput1">
            <Form.Control type="text" 
            className={classes.containerForm}
            rows={1} 
            name="palabraClave"
            value={newData.palabraClave || ""}
            onChange={handleInputChange} 
            />
          </Form.Group>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Close
            </Button>
            <Button variant="primary" type="submit" onClick={handleClose}>
            {palabraclave_id ? 'Guardar Cambios' : 'Crear'}
            </Button>
          </Modal.Footer>
        </Form>
      </Modal.Body>
    </Modal>
    
  )
};

export default ModalPalabraClave;